const InlineBlock = styled.span`
  display: inline-block;
  color: ${style.colorDefault};
`;

export const LessImpotsResults = ({ impots, isHeritage }) => {
  const refAmountBlock = useRef(null);
  const currentTotal = isHeritage ? impots.current : impots.current.total;
  const nextTotal = isHeritage ? impots.next : impots.revolution.total;

  const new_IR = impots.revolution?.IR;

  useEffect(() => {
    const HEIGHT = 200;

    let percentWin;
    if (isHeritage) {
      percentWin = (impots.current - impots.next) / impots.current;
    } else {
      percentWin = impots.gain / currentTotal || 0;
    }

    const pixelsWin = percentWin * HEIGHT;

    if (!impots || !refAmountBlock || !refAmountBlock.current) {
      return;
    }
    refAmountBlock.current.style.height = `${pixelsWin}px`;
  }, [impots, refAmountBlock]);

  return (
    <BlockLessImpots>
      <ResultsGraph>
        <Graph>
          <GraphGrid>
            {[1, 1, 1, 1, 1].map((v, i) => (
              <div key={i} />
            ))}
          </GraphGrid>

          <BlockGraph>
            <span>
              <span>vos impôts actuels</span>
              <br />
              <strong>{numeral(currentTotal).format("€0,0")} €</strong>
              {!isHeritage && <Fragment>&nbsp;/ an</Fragment>}
            </span>
            <BlockAmount />
          </BlockGraph>

          <BlockGraph>
            <span>
              <span>avec mélenchon</span>
              <br />
              <strong>
                {nextTotal < 0 ? 0 : numeral(nextTotal).format("€0,0")} €
              </strong>
              {!isHeritage && <Fragment>&nbsp;/ an</Fragment>}
            </span>

            <BlockAmountAnimated>
              <div ref={refAmountBlock} />
              <div />
            </BlockAmountAnimated>
          </BlockGraph>
        </Graph>

        <SourceImpots />
      </ResultsGraph>

      <ResultsText>
        <span>Si Jean-Luc Mélenchon est élu,</span>
        <ResultsNumber>
          {!isHeritage && nextTotal < 0 ? (
            <div style={{ textTransform: "none" }}>
              Vous ne payerez plus d’impôt sur le revenu. L'État vous versera{" "}
              {numeral(-new_IR).format("€0,0")}&nbsp;€, pour un gain net de{" "}
              {numeral(impots.gain).format("€0,0")}&nbsp;€.
            </div>
          ) : (
            <Fragment>
              {isHeritage ? (
                <Fragment>
                  Vos héritiers gagneront{" "}
                  {numeral(impots.current - impots.next).format("€0,0")}&nbsp;€
                  sur leur héritage
                </Fragment>
              ) : (
                <Fragment>
                  Vous gagnerez
                  <br />
                  <span>
                    {numeral(impots.gain).format("€0,0")}&nbsp;€ par an
                  </span>
                </Fragment>
              )}
            </Fragment>
          )}
        </ResultsNumber>

        {!isHeritage && nextTotal < 0 && (
          <ResultsHelper>
            <strong>Aujourd’hui en France</strong>, avoir un enfant à charge
            correspond à une réduction en pourcentage de vos impôts sur le
            revenu. C’est un <strong>système&nbsp;injuste</strong> qui favorise
            les familles plus riches avec des enfants alors que les non
            imposables ne touchent rien.
            <Spacer size="10px" />
            <strong>Avec la révolution fiscale</strong>, pour chaque enfant sera
            versé un crédit d'impôt dont tout le monde bénéfiera, indépendamment
            de ses revenus.
          </ResultsHelper>
        )}
      </ResultsText>
    </BlockLessImpots>
  );
};

export const MoreImpotsResults = ({ impots, isHeritage, isCouple = false }) => {
  const gain = isHeritage ? impots.next - impots.current : impots.gain;

  return (
    <BlockMoreImpots>
      {!isHeritage && !isCouple && (
        <span>
          votre revenu appartient aux {impots.percentile}% les plus élevés
        </span>
      )}
      <StyledContributionTitle>
        <Spacer size="16px" />

        {isNaN(gain) ? (
          <Fragment>
            <span>Vous contribuerez</span>
            <strong>BEAUCOUP pour la solidarité&nbsp;nationale :)</strong>
          </Fragment>
        ) : impots.next === 0 && impots.current === 0 ? (
          <Fragment>
            <strong>
              Votre patrimoine n'est pas soumis à des droits de succession
            </strong>
          </Fragment>
        ) : (
          <Fragment>
            <span>Vous contribuerez à hauteur de</span>
            <strong>
              {numeral(Math.abs(gain)).format("€0,0")}€ en + pour la
              solidarité&nbsp;nationale
            </strong>
          </Fragment>
        )}
      </StyledContributionTitle>

      <Spacer size="20px" />
      <StyledContribution>
        {isHeritage ? (
          impots.next === 0 && impots.current === 0 ? (
            <Fragment>
              Avec la Révolution Fiscale, l’impôt sur le patrimoine baissera ou
              n’augmentera pas pour 90&nbsp;% des contribuables.
            </Fragment>
          ) : impots.maxedOutTaxes ? (
            <Fragment>
              L’héritage maximum est plafonné à 8 millions d'euros, soit
              7&nbsp;siècles de SMIC. La somme au delà permettra notamment de
              financer <strong>une garantie d'autonomie de 1063€</strong> pour
              les jeunes détachés du foyer fiscal parental. Ce paiement pourra
              être étalé sur plusieurs années.
            </Fragment>
          ) : (
            <Fragment>
              Ce paiement pourra être étalé sur plusieurs années. Votre
              contribution permettra notamment de financer{" "}
              <strong>une garantie d'autonomie de 1063€</strong> pour les jeunes
              détachés du foyer fiscal parental.
            </Fragment>
          )
        ) : (
          <Fragment>
            Votre contribution permettra de{" "}
            <strong>
              renforcer les moyens de nos hôpitaux, de nos écoles, de notre
              justice, de notre police
            </strong>{" "}
            et d’engager les investissements indispensables pour la{" "}
            <strong>bifurcation&nbsp;écologique</strong>.
          </Fragment>
        )}
      </StyledContribution>
      <SourceImpots />
    </BlockMoreImpots>
  );
};

const Total = ({ impots, isRevolution }) => {
  const IR = !isRevolution ? impots.current.IR : impots.revolution.IR;
  const CSG = !isRevolution ? impots.current.CSG : impots.revolution.CSG;

  return (
    <StyledTotal isRevolution={isRevolution}>
      <StyledLabel isRevolution={isRevolution}>
        {!isRevolution
          ? "Votre imposition actuelle"
          : "Avec la révolution fiscale"}
      </StyledLabel>
      <Spacer size="8px" />
      <span>
        {numeral(IR).format("€0,0")} € / an &nbsp;
        <InlineBlock>impôt sur le revenu</InlineBlock>
      </span>
      <Spacer size="8px" />
      <span>
        {numeral(CSG).format("€0,0")} € / an &nbsp;
        <InlineBlock>CSG (contribution sociale généralisée)</InlineBlock>
      </span>
      <Spacer size="8px" />
      <div>
        <strong>TOTAL : {numeral(IR + CSG).format("€0,0")} €&nbsp;/ an</strong>
      </div>
    </StyledTotal>
  );
};

export const Details = ({ impots }) => (
  <BlockDetails>
    <span>Les chiffres en détails</span>
    <Spacer size="10px" />
    <StyledDetails>
      <Total impots={impots} />
      <Total isRevolution={true} impots={impots} />
    </StyledDetails>
  </BlockDetails>
);
